<template>
  <div class="login">
    <!-- 登录 -->
    <div class="login-bg">
      <div class="login-box">
        <div class="self-icon-phone">
          <img class="auto-img" src="../assets/images/phone.png" alt="" />
        </div>
        <van-field
          v-model="userInfo.phone"
          name="手机号"
          placeholder="phone"
          autocomplete="off"
          @keyup.enter="goLogin" 
        />
        <div class="self-icon-passwrod">
          <img class="auto-img" src="../assets/images/password.png" alt="" />
        </div>
        <van-field
          :type="isChange ? 'text' : 'password'"
          :right-icon="isChange ? 'eye' : 'closed-eye'"
          @click-right-icon="ToggleLight"
          v-model="userInfo.password"
          name="密码"
          placeholder="password"
          autocomplete="off"
          @keyup.enter="goLogin" 
        />
        <div class="commit-btn" @click="goLogin">Login</div>
        <div class="commit-btn" @click="toggleRegisterBox">Register</div>
      </div>
    </div>
    <!-- 注册 -->
    <van-popup v-model="isShow" position="bottom" :style="{ height: '40%' }">
      <div class="self-register-phone">
        <img class="auto-img" src="../assets/images/phone.png" alt="" />
      </div>
      <van-field
        v-model="userRegisterInfo.phone"
        name="手机号"
        placeholder="11位有效手机号"
        autocomplete="off"
      />

      <div class="self-register-password">
        <img class="auto-img" src="../assets/images/password.png" alt="" />
      </div>
      <van-field
        :type="isChange ? 'text' : 'password'"
        :right-icon="isChange ? 'eye' : 'closed-eye'"
        @click-right-icon="ToggleLight"
        v-model="userRegisterInfo.password"
        name="密码"
        placeholder="密码最少6位数"
        autocomplete="off"
      />

      <div class="self-register-id">
        <img class="auto-img" src="../assets/images/id.png" alt="" />
      </div>
      <van-field
        v-model="userRegisterInfo.nickName"
        name="昵称"
        placeholder="输入昵称"
        autocomplete="off"
      />
      <div class="commit-btn" @click="register">Register</div>
    </van-popup>
  </div>
</template>

<script>
//导入外部样式表
import "../assets/less/login.less";
//导入表单验证模块
import { validForm } from "../assets/js/validForm";
// 导入axios
import axios from "axios";
import VueAxios from "vue-axios";
export default {
  name: "Login",
  data() {
    return {
      isChange: false,
      //用户登录信息
      userInfo: {
        phone: "",
        password: "",
      },

      //用户注册信息
      userRegisterInfo: {
        phone: "",
        password: "",
        nickName: "",
      },
      //是否展示注册
      isShow: false,
      // appkey
      appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
    };
  },
  created() {},
  methods: {
    //切换password类型
    ToggleLight() {
      this.isChange = !this.isChange;
    },
    //切换注册框
    toggleRegisterBox() {
      this.isShow = true;
    },
    //注册
    register() {
      //构造表单验证信息
      let o = {
        phone: {
          value: this.userRegisterInfo.phone,
          errorMsg: "手机号格式不正确",
          reg: /^1[3-9]\d{9}$/,
        },
        password: {
          value: this.userRegisterInfo.password,
          errorMsg: "密码由数字字母下划线组合(6-16字符)",
          reg: /^[A-Za-z]\w{5,15}$/,
        },
        nickName: {
          value: this.userRegisterInfo.nickName,
          errorMsg: "昵称由字母数字下划线汉字组合(1-10字符)",
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
        },
      };

      let isPass = validForm.valid(o);
      if (isPass) {
        //复制用户注册信息
        let userInfo = Object.assign({}, this.userRegisterInfo);
        userInfo.appkey = this.appkey;
        axios.defaults.baseURL = "http://www.kangliuyong.com:10002";
        //启动加载提示
        this.$toast.loading({
          //文本提示
          message: "加载中...",
          //禁止穿透点击
          forbidClick: true,
          //提示时间, 0: 没有时间限制
          duration: 0,
        });

        //发起注册请求
        this.axios({
          //请求类型
          method: "POST",
          //请求路径
          url: "/register",

          //POST请求参数, object
          data: userInfo,
        })
          .then((result) => {
            //关闭加载提示
            this.$toast.clear();

            if (result.data.code == 100) {
              this.isShow = false;
            } else {
              //如果注册失败，手机被注册了
              this.$toast(result.data.msg);
            }
          })
          .catch((err) => {
            //关闭加载提示
            this.$toast.clear();
          });
      }
    },
    // 登录
    goLogin() {
      //构造表单验证信息
      let o = {
        phone: {
          value: this.userInfo.phone,
          errorMsg: "手机号格式不正确",
          reg: /^1[3-9]\d{9}$/,
        },
        password: {
          value: this.userInfo.password,
          errorMsg: "密码错误",
          reg: /^[A-Za-z]\w{5,15}$/,
        },
      };

      let isPass = validForm.valid(o);
      if (isPass) {
        //发起登录请求
        //复制用户注册信息
        let userInfo = Object.assign({}, this.userInfo);
        userInfo.appkey = this.appkey;

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
        });
        // 设置当前基础路径
        axios.defaults.baseURL = "http://www.kangliuyong.com:10002";
        //发起注册请求
        this.axios({
          //请求类型
          method: "POST",
          //请求路径
          url: "/login",

          //POST请求参数, object
          data: userInfo,
        })
          .then((result) => {
            this.$toast.clear();

            if (result.data.code == 200) {
              this.$toast(result.data.msg);
              //保存token, 以便后面验证登录
              localStorage.setItem("token", result.data.token);
              this.$router.push({ name: "Home" });
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch((err) => {
            this.$toast.clear();
          });
      }
    },
  },
};
</script>

<style lang="less" scoped>
</style>